/* ========== Base Styles ========== */
:root {
  --primary-color: #00ff9d;
  --secondary-color: #4169e1;
  --bg-dark: #0f1218;
  --bg-darker: #080c10;
  --text-primary: #e6e6e6;
  --text-secondary: #a0a0a0;
  --accent-color: #ff3864;
  --code-bg: #1c2333;
  --terminal-bg: #1a1e24;
  --card-bg: rgba(22, 28, 36, 0.8);
  --border-color: rgba(255, 255, 255, 0.1);
  --shadow-color: rgba(0, 255, 157, 0.1);
  --glow-shadow: 0 0 20px rgba(0, 255, 157, 0.2);

  --font-mono: 'Fira Code', 'JetBrains Mono', 'SF Mono', Menlo, Monaco, 'Courier New', monospace;
  --font-sans: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* Global styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--bg-dark);
  color: var(--text-primary);
  font-family: var(--font-sans);
  overflow-x: hidden;
  line-height: 1.6;
}

.enhanced-portfolio {
  position: relative;
}

/* Floating particles background */
.enhanced-portfolio::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    radial-gradient(circle at 25% 30%, rgba(0, 255, 157, 0.05) 0%, transparent 10%),
    radial-gradient(circle at 75% 60%, rgba(65, 105, 225, 0.05) 0%, transparent 20%);
  pointer-events: none;
  z-index: -1;
}

a {
  color: var(--primary-color);
  text-decoration: none;
  transition: all 0.3s ease;
}

a:hover {
  color: var(--accent-color);
  text-shadow: 0 0 8px rgba(255, 56, 100, 0.5);
}

button {
  font-family: var(--font-mono);
  cursor: pointer;
  border: none;
}

section {
  padding: 100px 2rem;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.section-header {
  margin-bottom: 3rem;
  position: relative;
  display: flex;
  align-items: center;
}

.section-title {
  font-family: var(--font-mono);
  font-size: 2.2rem;
  color: var(--primary-color);
  position: relative;
  margin-bottom: 1.5rem;
  display: inline-block;
}

.section-title::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, var(--primary-color), transparent);
  animation: pulseWidth 4s ease-in-out infinite;
}

/* ========== Loading Screen ========== */
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-darker);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loading-content {
  text-align: center;
  max-width: 600px;
}

.loading-text {
  font-family: var(--font-mono);
  color: var(--primary-color);
  margin-bottom: 2rem;
}

.terminal-text {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  white-space: nowrap;
  overflow: hidden;
  animation: typewriter 3s steps(30) infinite;
}

.prompt {
  color: var(--accent-color);
  margin-right: 0.5rem;
}

.progress-bar {
  width: 100%;
  height: 4px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  overflow: hidden;
  position: relative;
}

.progress {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  position: absolute;
  left: 0;
  top: 0;
  animation: loadProgress 2s ease-in-out;
  width: 100%;
  border-radius: 2px;
  box-shadow: 0 0 10px var(--primary-color);
}

/* ========== Navigation ========== */
.navigation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 3rem;
  z-index: 100;
  backdrop-filter: blur(10px);
  background: rgba(15, 18, 24, 0.8);
  border-bottom: 1px solid var(--border-color);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.navigation:hover {
  background: rgba(15, 18, 24, 0.95);
}

.nav-logo {
  display: flex;
  align-items: center;
}

.logo-text {
  font-family: var(--font-mono);
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--primary-color);
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.logo-text::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: var(--primary-color);
  transform: translateX(-100%);
  transition: transform 0.5s ease;
}

.logo-text:hover::after {
  transform: translateX(0);
}

.nav-links {
  display: flex;
  gap: 2rem;
  list-style: none;
}

.nav-links li {
  position: relative;
  font-family: var(--font-mono);
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
}

.nav-links li::before {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--primary-color);
  transition: width 0.3s ease;
}

.nav-links li:hover::before,
.nav-links li.active::before {
  width: 100%;
}

.nav-links li.active {
  color: var(--primary-color);
}

.nav-links li:hover {
  color: var(--primary-color);
  transform: translateY(-2px);
}

.nav-index {
  color: var(--primary-color);
  margin-right: 0.5rem;
  font-size: 0.8rem;
}

/* ========== Home Section ========== */
.home-section {
  background-color: var(--bg-darker);
  position: relative;
  overflow: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 120px;
}

.code-blocks {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.1;
}

.code-block {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 10px;
  background: repeating-linear-gradient(0deg,
      var(--code-bg),
      var(--code-bg) 30px,
      var(--bg-darker) 30px,
      var(--bg-darker) 31px);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  opacity: 0.3;
}

.code-block:nth-child(1) {
  top: 10%;
  left: 5%;
  transform: rotate(-15deg);
  animation: floatAnimation 15s ease-in-out infinite;
}

.code-block:nth-child(2) {
  top: 30%;
  right: 10%;
  transform: rotate(10deg);
  animation: floatAnimation 18s ease-in-out infinite reverse;
}

.code-block:nth-child(3) {
  bottom: 15%;
  left: 20%;
  transform: rotate(5deg);
  animation: floatAnimation 20s ease-in-out infinite;
}

.home-content {
  position: relative;
  z-index: 1;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
  animation: fadeInUp 1s ease-out forwards;
}

.greeting {
  font-family: var(--font-mono);
  color: var(--primary-color);
  font-size: 1.2rem;
  margin-bottom: 1rem;
  opacity: 0;
  animation: fadeInUp 0.8s ease forwards 0.2s;
}

.name {
  font-family: var(--font-mono);
  font-size: 3rem;
  margin-bottom: 1rem;
  opacity: 0;
  line-height: 1.3;
  animation: fadeInUp 0.8s ease forwards 0.4s;
}

.function {
  color: #569cd6;
  /* Blue for keywords */
}

.value {
  color: #ce9178;
  /* Orange for strings */
}

.title {
  font-size: 2rem;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  opacity: 0;
  animation: fadeInUp 0.8s ease forwards 0.6s;
}

.tagline {
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  opacity: 0;
  animation: fadeInUp 0.8s ease forwards 0.8s;
}

.cta-container {
  display: flex;
  gap: 1.5rem;
  opacity: 0;
  animation: fadeInUp 0.8s ease forwards 1s;
}

.cta-button {
  padding: 0.8rem 1.8rem;
  font-size: 1rem;
  border-radius: 5px;
  font-family: var(--font-mono);
  font-weight: 500;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.cta-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: all 0.5s ease;
}

.cta-button:hover::before {
  left: 100%;
}

.cta-button.primary {
  background-color: var(--primary-color);
  color: var(--bg-darker);
  border: none;
  box-shadow: 0 0 15px rgba(0, 255, 157, 0.3);
}

.cta-button.primary:hover {
  background-color: var(--primary-color);
  transform: translateY(-5px);
  box-shadow: 0 5px 20px rgba(0, 255, 157, 0.4);
}

.cta-button.secondary {
  background-color: transparent;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.cta-button.secondary:hover {
  background-color: rgba(0, 255, 157, 0.1);
  transform: translateY(-5px);
  box-shadow: 0 5px 20px rgba(0, 255, 157, 0.2);
}

.scroll-indicator {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  animation: fadeIn 1s ease 1.5s forwards;
}

.scroll-text {
  font-family: var(--font-mono);
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 10px;
  animation: pulseOpacity 2s ease-in-out infinite;
}

/* ========== About Section ========== */
.about-section {
  background-color: var(--bg-dark);
  position: relative;
  overflow: hidden;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 4rem;
  align-items: center;
}

.about-image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-container {
  width: 300px;
  height: 300px;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
  background-color: var(--code-bg);
  box-shadow: var(--glow-shadow);
  transition: all 0.5s ease;
}

.profile-container::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  z-index: -1;
  border-radius: 10px;
  background: linear-gradient(45deg, var(--primary-color), var(--secondary-color));
  animation: borderGlow 4s ease-in-out infinite alternate;
}

.profile-container:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 10px 30px rgba(0, 255, 157, 0.3);
}

.profile-image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
  font-weight: bold;
  color: var(--primary-color);
  background-color: var(--code-bg);
  position: relative;
}

.profile-image-placeholder::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg,
      rgba(15, 18, 24, 0.8) 0%,
      rgba(15, 18, 24, 0.4) 50%,
      rgba(15, 18, 24, 0) 100%);
  z-index: 1;
}

.about-text {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.about-bio {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
}

.about-bio.animate-in {
  opacity: 1;
  transform: translateY(0);
}

.terminal-window {
  background-color: var(--terminal-bg);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  border: 1px solid var(--border-color);
}

.terminal-header {
  background-color: rgba(0, 0, 0, 0.2);
  padding: 10px 15px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--border-color);
}

.terminal-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 8px;
  background-color: #ff5f56;
}

.terminal-button:nth-child(2) {
  background-color: #ffbd2e;
}

.terminal-button:nth-child(3) {
  background-color: #27c93f;
}

.terminal-title {
  margin-left: 10px;
  font-family: var(--font-mono);
  font-size: 0.8rem;
  color: var(--text-secondary);
}

.terminal-body {
  padding: 20px;
  font-family: var(--font-mono);
  font-size: 1rem;
  line-height: 1.8;
}

.terminal-body p {
  margin-bottom: 1rem;
}

.terminal-body .prompt {
  color: var(--primary-color);
}

.skills-container {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
}

.skills-container.animate-in {
  opacity: 1;
  transform: translateY(0);
}

.skills-container h3 {
  font-family: var(--font-mono);
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  font-weight: normal;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.skill-item {
  opacity: 0;
  animation: fadeInRight 0.5s ease forwards;
}

.skill-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.skill-name {
  font-family: var(--font-mono);
  color: var(--text-primary);
}

.skill-percentage {
  font-family: var(--font-mono);
  color: var(--primary-color);
}

.skill-bar {
  height: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}

.skill-level {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  border-radius: 4px;
  transition: width 1.5s cubic-bezier(0.19, 1, 0.22, 1);
  position: relative;
}

.skill-level::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 5px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5);
  animation: skillPulse 2s ease-in-out infinite;
}

/* ========== Projects Section ========== */
.projects-section {
  background-color: var(--bg-darker);
  position: relative;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

.project-card {
  background-color: var(--card-bg);
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid var(--border-color);
  transition: all 0.5s ease;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  position: relative;
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 0.8s ease forwards;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--glow-shadow);
  border-color: var(--primary-color);
}

.project-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
  position: relative;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.project-card:hover .project-image img {
  transform: scale(1.05);
}

.project-image::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg,
      rgba(15, 18, 24, 0.8) 0%,
      rgba(15, 18, 24, 0.4) 50%,
      rgba(15, 18, 24, 0) 100%);
}

.project-content {
  padding: 1.5rem;
}

.project-title {
  font-family: var(--font-mono);
  font-size: 1.3rem;
  margin-bottom: 0.8rem;
  color: var(--primary-color);
}

.project-description {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  line-height: 1.5;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tag {
  background-color: rgba(0, 255, 157, 0.1);
  color: var(--primary-color);
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-family: var(--font-mono);
  transition: all 0.3s ease;
}

.project-card:hover .tag {
  background-color: rgba(0, 255, 157, 0.2);
}

.project-link {
  display: inline-flex;
  align-items: center;
  font-family: var(--font-mono);
  color: var(--primary-color);
  font-size: 0.9rem;
  position: relative;
  overflow: hidden;
}

.project-link::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--primary-color);
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.4s ease;
}

.project-card:hover .project-link::after {
  transform: scaleX(1);
  transform-origin: left;
}

/* ========== Footer ========== */
.footer {
  background-color: var(--bg-darker);
  padding: 3rem 0;
  border-top: 1px solid var(--border-color);
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.footer-links {
  display: flex;
  gap: 2rem;
}

.footer-link {
  color: var(--text-secondary);
  font-family: var(--font-mono);
  transition: all 0.3s ease;
  position: relative;
}

.footer-link::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 1px;
  background-color: var(--primary-color);
  transition: width 0.3s ease;
}

.footer-link:hover::after {
  width: 100%;
}

.footer-link:hover {
  color: var(--primary-color);
}

.footer-copyright {
  font-family: var(--font-mono);
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.comment {
  color: var(--text-secondary);
}

.footer-back-to-top {
  cursor: pointer;
  font-family: var(--font-mono);
  color: var(--primary-color);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
}

.footer-back-to-top:hover {
  transform: translateY(-5px);
}

.back-to-top-text {
  position: relative;
}

.back-to-top-text::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 1px;
  background-color: var(--primary-color);
  transition: width 0.3s ease;
}

.footer-back-to-top:hover .back-to-top-text::after {
  width: 100%;
}

/* ========== Animations ========== */
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulseOpacity {

  0%,
  100% {
    opacity: 0.5;
  }

  50% {
    opacity: 1;
  }
}

@keyframes floatAnimation {

  0%,
  100% {
    transform: translateY(0) rotate(-15deg);
  }

  50% {
    transform: translateY(-20px) rotate(-10deg);
  }
}

@keyframes typewriter {
  0% {
    width: 0;
  }

  50% {
    width: 100%;
  }

  100% {
    width: 100%;
  }
}

@keyframes loadProgress {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

@keyframes borderGlow {
  0% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}

@keyframes skillPulse {

  0%,
  100% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@keyframes pulseWidth {

  0%,
  100% {
    width: 70%;
  }

  50% {
    width: 100%;
  }
}

/* ========== Matrix Code Rain Effect (Background) ========== */
.home-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
  opacity: 0.07;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5)),
    url("data:image/svg+xml,%3Csvg width='100' height='100' xmlns='http://www.w3.org/2000/svg'%3E%3Ctext x='10' y='20' fill='%2300ff9d' font-family='monospace'%3E01%3C/text%3E%3Ctext x='50' y='30' fill='%2300ff9d' font-family='monospace'%3E10%3C/text%3E%3Ctext x='30' y='50' fill='%2300ff9d' font-family='monospace'%3E01%3C/text%3E%3Ctext x='70' y='70' fill='%2300ff9d' font-family='monospace'%3E10%3C/text%3E%3Ctext x='10' y='90' fill='%2300ff9d' font-family='monospace'%3E01%3C/text%3E%3C/svg%3E");
}

/* ========== Media Queries ========== */
@media (max-width: 1200px) {
  .section-title {
    font-size: 2rem;
  }

  .about-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .about-image {
    order: 1;
  }

  .about-text {
    order: 2;
  }
}

@media (max-width: 768px) {
  .navigation {
    padding: 1rem 1.5rem;
  }

  .nav-links {
    gap: 1rem;
  }

  section {
    padding: 80px 1.5rem;
  }

  .home-content {
    padding: 0;
  }

  .name {
    font-size: 2.5rem;
  }

  .title {
    font-size: 1.5rem;
  }

  .cta-container {
    flex-direction: column;
    gap: 1rem;
  }

  .cta-button {
    width: 100%;
  }
}